import React, { useState } from "react";
import "../App.css";
import { useAuth } from "../contexts/AuthContext";
import { Link, useHistory } from "react-router-dom";
import { Alert } from "react-bootstrap";

export default function Navbar() {
  const [click, setClick] = useState(false);

  const handleClick = () => setClick(!click);
  const closeNavbar = () => setClick(false);

  const [error, setError] = useState("");
  const { logout } = useAuth();
  const history = useHistory();

  async function handleLogout() {
    setError("");

    try {
      await logout();
      history.push("/login");
    } catch {
      setError("Failed to log out");
    }
  }
  //<strong>{currentUser.username}</strong>
  return (
    <div>
      {error && <Alert variant="danger">{error}</Alert>}

      <nav className="">
        <div className="nav_icon" onClick={handleClick}>
          <i className={click ? "fas fa-times" : "fas fa-bars"} />
        </div>

        <ul
          className={`ul_container w-100 align-items-center justify-content-between d-flex ${
            click ? "nav-menu active" : "nav-menu"
          }`}
        >
          <div className="d-flex">
            <Link to="/" className="pr-3 bottom_line" onClick={closeNavbar}>
              <li className="li_home">Home</li>
            </Link>

            <Link to="/zone" className="pr-3 li_zone bottom_line">
              <li className=" li_home"> About </li>
            </Link>
            <Link to="/zone" className="pr-3 li_zone bottom_line">
              <li className=" li_home"> Portfolio </li>
            </Link>
          </div>

          <Link
            to="/login"
            className="pr-3 li_zone bottom_line"
            onClick={handleLogout}
          >
            <li className=" li_home">Sign Out </li>
          </Link>
        </ul>
      </nav>
    </div>
  );
}
